body {
  margin: 0;
  font-family: "Poppins", sans-serif;
}

.banner {
  background-color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 20px;
  width: 100%;
}

.banner .content {
  /* max-width: 700px; */
  display: -webkit-flex;
  display: flex;
}

.content .leader{
  margin: 10px 0px;
}
.b {
  margin-bottom: 10px;
  /* font-size: 4em; */
  color: black;
}
.banner .content h1 {
  font-size: 3.5em;
}
.banner .content p {
  margin-top: 10px;
  font-size: 1.5em;
}

.banner .image {
  max-width: 350px;
  /* margin-left: 50px; */
}

.t {
  margin: 10px 0px;
  margin-left: 20px;
}
.t h1 {
  letter-spacing: 2px;
  text-align: center;
}
.t p {
  text-align: center;
}
@media only screen and (max-width: 872px) {
  .banner .content {
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
  }

  .banner .content h1 {
    font-size: 3em;
  }
  .banner .content p {
    font-size: 1.5em;
  }
  .banner {
    -webkit-justify-content: center;
            justify-content: center;
  }
  .banner .image {
    max-width: 300px;
    height: auto;
    margin-left: 0px;
  }
}

@media only screen and (max-width: 500px) {
  .leader, .t{
   margin: 14px auto;
   -webkit-justify-content: center;
           justify-content: center;
  }
  .banner .content h1 {
    font-size: 1.8em;
  }
  .banner .content p {
    font-size: 1em;
  }
  .banner {
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  .leader .image{
    max-width: 250px;
    margin: 14px auto;
  }
}

.FilterDiv{
    box-shadow: 0px 0px 8px rgba(46, 45, 45, 0.658);
    margin:20px 0px;
    margin-top: 50px;
    padding: 10px;
    border-radius: 8px;
    width: 100%;
  
  }
  
  .FilterDiv h2{
      /* font-weight:300; */
      margin: 10px;
      text-align: center;
  }
  .Filters{
      display: -webkit-flex;
      display: flex;
      width: auto;
      -webkit-justify-content: center;
              justify-content: center;
     
      -webkit-flex-wrap: wrap;
     
              flex-wrap: wrap;
      margin: 14px 0px;
  }
  
  .Filters .FilterItem{
      width: 30%;
      min-width: 300px;
      /* margin: 8px; */
  }
  .FilterItem{
      margin-right: 30px;
  }
  
  .submitbtn{
      width: 30%;
      margin: auto;
      min-width: 300px;
  }
  .submitbtn button{
      width: 100%;
      background-color: #ec7c2d;
      color: #fff;
      border: 1px solid white;
      padding: 8px;
      border-radius:8px ;
      font-size: 18px;
  }
  
  .submitbtn button:hover{
  
      background-color: gray;
  }
  
  /* .submitbtn span.icon{
      margin-right: 8px;
      height: 18px;
  } */
  .FilterItem select{
      width: 100%;
      padding: 8px 10px;
      border-radius: 4px;
      outline: none;
      border: 1px solid black;
  }
  .FilterItem select:focus{
      border:1px solid #ec7c2d ;
  }
  
  
  /* Media Quaries */
  
  
  
  @media only screen and (max-width:1000px){
      .Filters{
          -webkit-justify-content: center;
                  justify-content: center;
      }
      .FilterItem{
          margin: 8px;
      }
  
  
      
      .Filters .FilterItem,.submitbtn{
          width: 95%;
      }
  }
  
  @media only screen and (max-width:650px){
      .Filters .FilterItem{
          width: 95%;
          min-width: 200px;
      
      }
  }
